<template>
    <div class="container">
      <h1>商品库存列表</h1>
      <el-button type="primary" class="addItem" @click="addInventory0()">库存建账</el-button>
      <div class="tableContainer">
        <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="ID"
        width="80">
      </el-table-column>
      <el-table-column
        prop="image"
        label="图片">
            <template slot-scope="scope">
              <div v-html="scope.row.image"></div>
            </template>
      </el-table-column>
      <el-table-column
        prop="goods_id"
        label="商品ID"
        width="180">
      </el-table-column>
      <el-table-column
        prop="sku"
        label="SKU">
      </el-table-column>

      <el-table-column
        prop="spu"
        label="SPU">
      </el-table-column>
      <el-table-column
        prop="balance_count"
        label="剩余库存">
      </el-table-column>

      <el-table-column
        prop="afloat_count"
        label="在途库存">
      </el-table-column>

      <el-table-column
        prop="lock_count"
        label="锁定库存">
      </el-table-column>

      <el-table-column
        prop="warehouse_id"
        label="仓库地址">
      </el-table-column>

      

      <el-table-column
        prop="color"
        label="颜色">
      </el-table-column>

      <el-table-column
        prop="size"
        label="尺寸">
      </el-table-column>



      <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleDetail(scope.row)" type="text" size="small">查看流水明细</el-button> 
            </template>
        </el-table-column>


      </el-table>
        
      </div>
    </div>
</template>

<script>
    
  export default {
    name: "warehouse",
    data() {
        return {
          tableData: [/*{
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },
          {
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },*/ 
          ],
          totalSize: 0,
          currentPage: 1,
          pageSize: 20
        }
    },
    methods: {
      filterDegree() {

      },
      filterStatus() {

      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      addInventory() {
        this.$router.push('/inventory/add')
      },
      handleDetail(row) {
        this.$router.push({ path: '/inventory/detail',query: row})
      },
       
      initTable() {
        let initList = [];
        let url = "http://api.orkowms.me/inventory?page="+this.currentPage+"&per-page="+this.pageSize;
        
        let that = this;
        let config = {
          headers : { 
            'Content-Type':'application/json;charset=UTF-8'
          },
        };
        console.log(111)
        this.$axios.get(url,config).then((res) => { 
          console.log(222)
          if(res.data.data){
            console.log(333)
            let tasksList = res.data.data ;
            that.totalSize = parseInt(res.data._meta.totalCount);
            tasksList.forEach((task) => {
              let tempTask = {};
              tempTask.id = task.id;
              tempTask.goods_id = task.goods_id;
              tempTask.balance_count = task.balance_count;
              tempTask.afloat_count = task.afloat_count;
              tempTask.lock_count = task.lock_count;
              tempTask.warehouse_id = task.warehouse_id;
              tempTask.sku = task.sku; 
              tempTask.color = task.color; 
              tempTask.size = task.size; 
              tempTask.image = "<img width='100' src='"+task.image+"'>";  
              tempTask.spu = task.spu;  

              initList.push(tempTask);
            })
          }
          that.tableData = initList;
          console.log(444)
          console.log(res)
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style lang="stylus">
  .container
    padding 30px
    position relative
    .addItem
      margin 20px 0
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 148px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



